@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.sidenav {
	position: fixed;
	top: 0;
	left: 0;
	width: 230px;
	height: 100vh;

	background-color: black;
	padding: 20px;
}

.logo-img {
	width: 70%;
	margin-bottom: 36px;
}

.arrow-text {
	color: #aeaeae;

	font-size: 1.4em;
	padding: 0 5px;
	text-align: center;
}

.arrow-img {
	max-width: 160px;
	position: relative;
	top: 30px;

	display: block;
	margin: 0 auto;
}

.not-sidenav {
	margin-left: 230px;
}

.playlist-meta {
	color: #ffffff;
	background: linear-gradient(#7c83b6, #444864);

	height: 42%;
	width: 100%;

	display: flex;
	align-items: flex-end;

	padding: 65px 32px 25px 30px;
}

.playlist-cover-img {
	width: 192px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	float: left;
}

.all-playlist-text {
	margin-left: 24px;
	font-weight: 600;
}

.text-playlist {
	font-size: 0.8em;
	line-height: 90%;
}

.text-sorting-algorithms {
	font-size: 3.5em;
	font-weight: 800;
}

.text-all-the-best,
.stats {
	font-size: 0.85em;
	color: #c2c2cc;
	margin-top: 2px;
}

.stats {
	margin-top: 6px;
}

.stats a {
	color: white;
	text-decoration: none;
}

.stats span:before {
	content: '   •   ';
}

.playlist-wrapper {
	position: relative;
	width: 100%;
	height: auto;
}

.bg-grad {
	background: linear-gradient(#363a50, #121212);
	height: 300px;
}

.playlist {
	position: absolute;
	top: 0;
	left: 0;

	padding: 2em;
}

.accordion__section {
	display: flex;
	flex-direction: column;
}

.accordion {
	background: rgba(0, 0, 0, 0);
	color: #d0d0d0;
	cursor: pointer;
	padding: 10px 16px;
	display: flex;
	align-items: center;
	border: none;
	outline: none;
}

.accordion:hover {
	border-radius: 5px;
	background-color: rgba(255, 255, 255, 0.1);
	color: #f9f9f9;
}

.accordion:hover .artist-name {
	color: #f9f9f9;
}

.active {
	border-radius: 5px 5px 0 0;
	background-color: rgba(255, 255, 255, 0.1);
	color: #f9f9f9;
}

.active .artist-name {
	color: #f9f9f9;
}

.active:hover {
	/* conceal lower border radius */
	border-radius: 5px 5px 0 0;
	background-color: rgba(255, 255, 255, 0.1);
}

.accordion__icon {
	margin-left: auto;
	transition: transform 0.6s ease;
}

.rotate {
	transform: rotate(90deg);
}

.accordion__content {
	background-color: rgba(255, 255, 255, 0.1);
	/*overflow: auto;*/
	transition: max-height 0.6s ease;

	box-sizing: content-box;
	overflow: hidden;

	border-radius: 0 0 5px 5px;

	padding: 0 50px;
}

.accordion__content::-webkit-scrollbar {
	display: none;
}

.ranking {
	font-size: 16px;
	margin-right: 14px;
}

.song-and-artist {
	width: 200px;
	margin-right: 40px;
}

.song-name {
	font-family: 'Open Sans', sans-serif;
	font-weight: 600;
	font-size: 16px;
	text-align: left;
	color: #f9f9f9;
}

.artist-name {
	font-size: 14px;
	color: #b3b3b3;
	text-decoration: none;

	float: left;
	text-align: left;
}

.artist-name:hover {
	text-decoration: underline;
}

.song-image {
	height: 3em;
	margin-right: 16px;
}

.album-name {
	font-size: 14px;
	width: 160px;
	margin-right: 70px;

	text-align: left;
}

.case {
	text-align: left;
	margin-right: 40px;
	width: 70px;
}

.worst {
	margin-right: 0;
}

.visualizer {
	position: relative;
	width: 360px;
	height: 300px; /* max possible bar height */
	display: inline-block;

	overflow: hidden;
}

.right {
	float: right;
}

.code {
	position: relative;
	width: 390px;
	max-width: 390px;
	display: inline-block;

	background: rgba(156, 160, 202, 0.3);
}

.code pre {
	font-family: 'Courier New', monospace;
	font-size: 14px;
	margin: 10px 10px;
	color: white;
}

.button-group {
	position: relative;
	float: bottom;
}

.button-group button {
	background-color: #118ab2;
	color: #f2f3ec;
	padding: 10px 24px;
	cursor: pointer;
	width: 100%;
	display: block;
}

.button-group button:not(:last-child) {
	border-bottom: none; /* Prevent double borders */
}

.button-group button:hover {
	background-color: #073b4c;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;

	font-family: 'Open Sans', sans-serif;
}

html {
	background-color: #121212;
	overflow-x: hidden;
}

